export const referencedSchema = {
  type: 'object',
  properties: {
    str: { type: 'string' },
    obj: {
      type: 'object',
      properties: {
        cascStr: { type: 'string' },
        cascNum: { type: 'number' }
      }
    }
  },
  required: ['str']
};

export const evaluationSchema = {
  'type': 'object',
  'properties': {
    'name': { 'type': 'string'},
    'birthday': {
      'type': 'string',
      'format': 'data'
    },
    'alive': { 'type': 'boolean' },
    'address': {
      'properties': {
        'street': { 'type': 'string' },
        'city': { 'type': 'string'}
      }
    }
  },
  'required': ['name', 'birthday']
};



